.wp-block-blocks-gamestore-single-news {
	margin-top: 0;
	margin-bottom: clamp(3.75rem, 2.77rem + 4.92vw, 7.5rem);

	.featured-image-news {
		margin-top: 0;
		margin-bottom: clamp(3.75rem, 2.77rem + 4.92vw, 7.5rem);
		position: relative;
		box-sizing: border-box;
		z-index: 1;
		background-size: cover;
		padding-bottom: clamp(3.75rem, 2.77rem + 4.92vw, 7.5rem);

		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: var(--mask-gradient);
			z-index: -1;
		}

		.wrapper {
			position: relative;
			z-index: 2;
			display: flex;
			flex-direction: column;
			justify-content: start;
			padding-top: 65px;
		}

		.news-meta {
			display: flex;
			align-items: center;
			gap: 8px;
			color: var(--text-secondary, rgba(255, 255, 255, 0.70));
			font-size: 20px;
			font-style: normal;
			font-weight: 400;
			line-height: 1.6;

			>div {
				display: flex;
				align-items: center;
				gap: 8px;
			}
		}
	}

	.news-container {
		display: flex;
		align-items: start;
		justify-content: space-between;
		gap: 48px;

		.news-social-share {
			width: 200px;
			display: flex;
			flex-direction: column;
			gap: 8px;
			color: var(--text-primary, #0E0D0F);
			font-size: 14px;
			font-weight: 700;
			line-height: 1.4;
			letter-spacing: 2.1px;
			text-transform: uppercase;

			.social-share-buttons {	
				display: flex;
				gap: 8px;

				a {
					width: 40px;
					height: 40px;
					border-radius: 12px;
					background: var(--purple-main, #6F76C0);
					backdrop-filter: blur(12px);
					font-size: 0;
					line-height: 0;

					&::before {
						content: '';
						display: block;
						width: 100%;
						height: 100%;
