					}
				}
			}
		}

		.news-content {
			width: calc(100% - 248px);
			p, img {
				margin-top: 24px;
				margin-bottom: 24px;

				&:first-child {
					margin-top: 0;
				}
				&:last-child {
					margin-bottom: 0;
				}
			}

			a {
				color: var(--purple-main, #6F76C0);
				text-decoration: none;
			}
			ul, ol {
				margin-left: 8px;
			}

			ul li {
				&::before {
					content: 'â€¢';
					margin-right: 8px
				}
			}

			img {
				border-radius: 24px;
			}
		}
	}
}